.container {
  margin: 25px;
  width: 460px;
  height: calc(100% - 50px);
  background-color: #454E54;
  position: fixed;
  display: flex;
  flex-direction: column;
  border-radius: 10px; }

.navHeading {
  color: #85898b;
  font-size: 1.3rem;
  margin: 2rem 0 1rem 1rem; }

.sidebar {
  padding: 0 0.5rem;
  width: 100%;
  padding-bottom: 1.5rem; }
  .sidebarEl {
    padding: 0.2rem 0;
    font-size: 1.5rem;
    color: #85898b;
    margin: 0.2rem 0;
    display: flex;
    width: 100%;
    align-items: center; }
    .sidebarEl:hover {
      background-color: #1E2024;
      cursor: pointer;
      color: #f2f1ef;
      border-radius: 6px; }
  .sidebar_icon {
    font-size: 2rem;
    padding: 1rem;
    margin: 0 1rem 0 1rem;
    color: #85898b; }

.user {
  display: flex;
  flex-basis: row;
  vertical-align: middle;
  margin: 25px 0; }

.ppicture {
  height: 70px;
  width: 70px;
  border-radius: 35px;
  margin-left: 25px;
  margin-right: 15px;
  object-fit: cover; }

.username {
  display: block;
  color: white;
  font-size: 21pt;
  line-height: 30pt; }

.status {
  display: block;
  color: #C6C6D9;
  font-size: 12pt;
  line-height: 16pt; }

.online {
  position: absolute;
  top: 70px;
  left: 70px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #66BB6A;
  border: solid #454E54 7px; }
